/* Import the fonts */
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: bold;
  src: url("../../assets/fonts/Poppins-Bold.ttf");
}

@font-face {
  font-family: "Poppins Light";
  font-style: normal;
  font-weight: 300;
  src: url("../../assets/fonts/Poppins-Light.ttf");
}

/* Navigation Bar Styles */
.navigation-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #000;
  border-bottom: 1px solid #eee;
  height: 36px;
  position: relative;
}

/* Phone Number Styles */
.phone-container {
  display: flex;
  align-items: center;
  gap: 10px; /* Add space between phone icon and number */
}

.phone-number {
  color: #2fb457; /* Green color from your CSS */
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  transition: all 0.3s ease;
}

.phone-number:hover {
  transform: scale(1.05);
  text-decoration: none;
  opacity: 0.9;
}

/* Add a subtle indication that it's clickable */
.phone-container {
  position: relative;
  cursor: pointer;
}

/* Logo Image Styles */
.logo-image {
  height: 24px; /* Adjust based on your desired size */
  width: auto;
  display: block;
}

.nav-links {
  display: flex;
  gap: 20px;
  margin: 0 auto; /* Center the navigation links */
}

.nav-link {
  color: #2fb457; /* Green color from your CSS */
  font-family: "Poppins Light", sans-serif;
  font-size: 12px;
  font-weight: 300;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.3s ease;
  position: relative;
  padding-bottom: 3px;
}

.nav-link:hover {
  opacity: 0.8;
}

/* Active link indicator */
.nav-link.active {
  font-weight: 500;
}

.nav-link.active::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #2fb457;
  transform: scaleX(1);
}

/* Hover animation for non-active links */
.nav-link:not(.active)::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #2fb457;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.nav-link:not(.active):hover::after {
  transform: scaleX(1);
}

/* Social Icons Container */
.social-icons {
  display: flex;
  gap: 10px;
}

.social-icon {
  display: block;
  width: 14px;
  height: 14px;
  text-decoration: none;
}

/* Placeholder for actual icons */
.icon-placeholder {
  width: 100%;
  height: 100%;
  background-color: #2fb457;
  border-radius: 50%;
  opacity: 0.8;
  transition: opacity 0.3s ease;
}

.social-icon:hover .icon-placeholder {
  opacity: 1;
}

/* Responsive design - IMPROVED */
@media (max-width: 768px) {
  .navigation-bar {
    flex-direction: column;
    height: auto;
    padding: 8px; /* Reduced padding */
    gap: 8px; /* Reduced gap */
  }
  
  .logo-container {
    margin-bottom: 4px; /* Small space after logo */
  }

  .nav-links {
    flex-direction: row; /* Keep links in a row for compact layout */
    justify-content: center;
    gap: 12px; /* Reduced gap between links */
    width: 100%;
    margin: 4px 0; /* Reduced margin */
  }
  
  .nav-link {
    font-size: 11px; /* Slightly smaller font */
  }

  .phone-container {
    margin-top: 4px; /* Small space before phone */
  }
  
  /* For very small screens, stack the nav links */
  @media (max-width: 480px) {
    .nav-links {
      flex-direction: column;
      align-items: center;
      gap: 6px; /* Even smaller gap for vertical layout */
    }
  }
}